import MarkdownRenderer from "../Components/MarkdownRenderer";
import PerguntasForm from "../Components/PerguntasForm";

export const M3A1 = () =>
{
const perguntas = [
    {
        id: '101',
        texto: 'O que so registros vocais?',
        tipo: 'aberta',
        respostaCorreta: 'So formas distintas de vibrao das pregas vocais que geram diferentes qualidades sonoras.'
    },
    {
        id: '102',
        texto: 'Qual registro vocal  mais usado para notas graves?',
        tipo: 'fechada',
        opcoes: ['Voz de cabea', 'Voz mista', 'Voz de peito'],
        respostaCorreta: 'Voz de peito'
    },
    {
        id: '103',
        texto: 'Para que serve a voz mista na tcnica vocal?',
        tipo: 'aberta',
        respostaCorreta: 'Ela suaviza a transio entre a voz de peito e a de cabea, mantendo uniformidade no timbre.'
    },
    {
        id: '104',
        texto: 'Qual das opes  caracterstica da voz de cabea?',
        tipo: 'fechada',
        opcoes: ['Vibrao torcica', 'Ressonncia nas cavidades superiores', 'Uso exclusivo de consoantes'],
        respostaCorreta: 'Ressonncia nas cavidades superiores'
    },
    {
        id: '105',
        texto: 'O que  falsete?',
        tipo: 'fechada',
        opcoes: ['Uma voz grave com presso', 'Um modo de emisso com som leve e arejado', 'Uma tcnica de respirao'],
        respostaCorreta: 'Um modo de emisso com som leve e arejado'
    },
    {
        id: '106',
        texto: 'O que caracteriza a mudana de registro vocal?',
        tipo: 'aberta',
        respostaCorreta: 'A mudana na coordenao das pregas vocais, afetando a qualidade sonora e a sensao de ressonncia.'
    }
];

return (
        <main>
            <MarkdownRenderer filePath="/michaelsilvacanto/aulas/modulo-3-aula-1.md" />
            <PerguntasForm perguntas={perguntas} />
        </main>
    );
}

