import MarkdownRenderer from "../Components/MarkdownRenderer";
import PerguntasForm from "../Components/PerguntasForm";

export const M2A1 = () => {
const perguntas = [
    {
        id: '21',
        texto: 'O que  altura na msica?',
        tipo: 'aberta',
        respostaCorreta: ' a propriedade do som que determina se ele  grave ou agudo, relacionada  frequncia da vibrao sonora.'
    },
    {
        id: '22',
        texto: 'Qual  a principal diferena entre timbre e intensidade?',
        tipo: 'aberta',
        respostaCorreta: 'O timbre diferencia as vozes e instrumentos mesmo com a mesma nota; a intensidade se refere ao volume do som.'
    },
    {
        id: '23',
        texto: 'O que influencia diretamente na durao de uma nota no canto?',
        tipo: 'aberta',
        respostaCorreta: 'A respirao eficiente e o apoio diafragmtico influenciam no tempo que uma nota  sustentada com qualidade.'
    },
    {
        id: '24',
        texto: 'Qual das opes abaixo representa uma nota com maior durao?',
        tipo: 'fechada',
        opcoes: ['Semibreve', 'Colcheia', 'Semnima'],
        respostaCorreta: 'Semibreve'
    },
    {
        id: '25',
        texto: 'A dinmica musical influencia na expressividade da interpretao?',
        tipo: 'fechada',
        opcoes: ['Sim', 'No'],
        respostaCorreta: 'Sim'
    },
    {
        id: '26',
        texto: 'Qual das propriedades do som est relacionada ao brilho ou corpo da voz?',
        tipo: 'fechada',
        opcoes: ['Timbre', 'Altura', 'Intensidade'],
        respostaCorreta: 'Timbre'
    }
];

    return (
        <main>
            <MarkdownRenderer filePath="/michaelsilvacanto/aulas/modulo-2-aula-1.md" />
            <PerguntasForm perguntas={perguntas} />
        </main>
    );
}

